<template>
  <div class="main">
    
    <header>
      <h1>CLEAN CHECK</h1>
    </header>

    <section id="content">
      <h2><strong>Thanks for scanning with Digital Seat!</strong></h2>
      <p>You are viewing cleaning information for the following:</p>

        <div class="info">
        <p><strong>American Airlines Center<br>
        Section: 106<br>
        Row: 10<br>
        Seat: 3</strong></p>
        <p class="blue"><strong>Seat was Last Cleaned on:<br>
        {{lastCleaned}} {{timeDiff}}</strong></p>
        </div>

      <p>By pressing the “Submit” button on this page, you are certifying that you have cleaned the seat listed above.</p>
      <div class="button" @click="submitCleaned"><strong>SUBMIT</strong></div>
    </section>

  </div>

</template>

<script>
import moment from 'moment'
import {api} from '@/utils/api'

export default {
  name: 'Employee',
  data () {
    return {
      lastCleaned: null,
      timeDiff: null
    }
  },
  mounted () {
    api.getRaw('gp/test').then( response => {
      this.lastCleaned = moment(response.data.lastCleaned).format("M/DD/YYYY")
      this.timeDiff = `${response.data.timeDiff}`
    }).catch( err => {
      console.log(err)
    }) 
  },
  methods: {
    submitCleaned() {
      api.rawPost('gp/test',{tagId:'DEMO'}).then( () => {
        this.$router.push({name: 'Cleaned'})
      }).catch( err => {
        console.log(err)
      })    
    }
  }
}
</script>

<style>
.employee-out {
  width: 100%;
  padding: 4%;
  background-color: rgba(73,119,203, .1);
  border-radius: .5em;
}

.bolded {
  font-weight: bolder;
}
</style>